.header {
    display: flex;
    justify-content:space-between;
    align-items:center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg {
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}

.nav-menu {
    display: flex;
}

.nav-menu li {
    padding: 0 1rem;
    list-style: none;
    
}

.nav-menu a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
    cursor: pointer;
}


.logo-Image {
    width: 40px; /* Adjust the width as needed */
    height: auto;
    margin-right: 10px; /* Adjust the margin as needed */
}

.header a {
    display: flex;
    align-items: center;
}

.footerGroup {
    display: flex;
    align-items: center;
}


/* MEDIA DEVICE*/

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.85);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition:  0.3s;
    }

    .nav-menu.active {
        left: 0;
    }
    
    .nav-menu li {
        padding: 1rem 0;
    }
    
    .nav-menu li a {
        font-size: 1.75rem;
    }

    .hamburger {
        display: initial;
    }
}